import React, { useState } from "react";
import Alert from "../Alert";
export default function QueryItemFullscreen(props) {
  const [DeletedQuery, setDeletedQuery] = useState(false)
  const {query,query_id,date,email} =props.allProps
  const deleteQuery =()=>{
    fetch(`http://localhost:8080/api/query/${query_id}`,{method:'delete'})
    setDeletedQuery(true)
    props.setClickedOnShowQuery(false)
  }
  return (
    <>
    {DeletedQuery && <Alert text="Deleted message sucessfully" colour="danger" />}
      <div className="container my-2 form-container form-floating ">
        <input
          defaultValue={email}
          className="my-3 form-control"
          type="email"
          name="email"
        />
        <textarea
          className="my-4 form-control"
          defaultValue={query}
          name="query"
          style={{ height: 100 }}
        ></textarea>
        <input type="text" className="my-3 form-control"defaultValue={query_id} name="" id="" />
        <input type="text" className="my-3 form-control" defaultValue={date} name="" id="" />
        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
        <button onClick={()=>{props.setClickedOnShowQuery(false)}} className="btn btn-primary">Back</button>
        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
         <button onClick={deleteQuery} className="btn btn-primary">Delete</button>
      </div>
      </>
  );
}
