import { HashRouter as Router, Route, Switch } from "react-router-dom";
import About from "./components/About";
import AdminWrapper from "./components/AdminWrapper";
import Footer from "./components/Footer";
import Form from "./components/Form";
import Home from "./components/Home";
import Navbar from "./components/Navbar";
import Publications from './components/Publication';
import './css/App.css';
export default function App() {
  return (
    <div className="App">
       <Router>       
           <Navbar />       
        <div className="body">
          <Switch>
            <Route exact path="/" component={Home}></Route>
            <Route exact path="/home" component={Home}></Route>
            <Route exact path="/admin" component={AdminWrapper}></Route>
            <Route exact path="/about" component={About}></Route>
            <Route exact path="/publications" component={Publications}></Route>
            <Route exact path="/post"><Form showAlert={false}/></Route>
            <Route exact path="/post/success"><Form success={true}/></Route>
            <Route exact path="/post/failure"><Form failure={true}/></Route>
          </Switch>       
        </div> 
        </Router>   
        <div className="footer">
          <Footer />
        </div>
    </div>
  );
}
