import React, { Component } from "react";
import Item from "./items/admin_item";
const MAX_OFFSET = 10;
let backButtonStyle, nextButtonStyle;
export default class Admin extends Component {
  constructor(props) {
    super(props);
    this.state = {
      Items: [],
      Offset: 0,
    };
  }
  async componentDidMount() {
    backButtonStyle = Array.from(document.getElementsByClassName("back"))[0]
      .styles;
    nextButtonStyle = Array.from(document.getElementsByClassName("next"))[0]
      .styles;
    fetch(`http://localhost:8080/api/query/?offset=0`)
      .then((res) => res.json())
      .then((data) => {
        this.setState(()=>{return {Items:data.content.reverse()}});
      });
    this.ajaxForQueries();
  }
  ajaxForQueries() {
      setInterval(async() =>{
      const data=await fetch(`http://localhost:8080/api/query/?offset=0`)
      const parsedData=await data.json();
      if(parsedData.reverse()!==this.state.Items) this.setState(()=>{return {Items:parsedData.reverse()}});
      },1000*60*20)
  }
  async componentDidUpdate(prevProps, prevState) {
    if (this.state.Offset === 1) backButtonStyle = { cursor: "none" };
    if (this.state.Offset === MAX_OFFSET) nextButtonStyle = { cursor: "none" };
    if (this.state.Offset !== 1 && backButtonStyle === { cursor: "none" })
      backButtonStyle = { cursor: "pointer" };
    if (this.state.Offset !== MAX_OFFSET &&nextButtonStyle === { cursor: "none" })
      nextButtonStyle = { cursor: "pointer" };
  }

  render() {
    const onClickNext = async ()=>{   
      const data = await fetch(`http://localhost:8080/api/query/?offset=${this.state.Offset+1}`);
      const parsedData = await data.json();
      this.setState({Items: parsedData.content.reverse(),Offset:this.state.Offset+1});
    }
    const onClickBack=async()=> {
      const data = await fetch(`http://localhost:8080/api/query/?offset=${this.state.Offset-1}`);
      const parsedData = await data.json();
      this.setState({Items: parsedData.content.reverse(),Offset:this.state.Offset-1});
    }
    return (
      <>
        <h1>Welcome Alok</h1>
        <div className="container my-container">
          <Item number="S.no" query_id="Query Id" query="query" email="email" date="Date" />
          {this.state.Items.map((item) => (
            <Item
              setClickedOnShowQuery={this.props.setClickedOnShowQuery}
              setFullScreenProps={this.props.setFullScreenProps}
              isLink={true}
              key={item.id}
              number={this.state.Items.indexOf(item)+1}
              query_id={item.id}
              query={item.query}
              email={item.email}
              date={item.date}
            />
          ))}
          <button
            onClick={onClickBack}
            className="btn back btn-danger btn-sm page"
          >
            Back
          </button>
          <button
            onClick={onClickNext}
            className="btn next btn-danger btn-sm page"
          >
            Next
          </button>
        </div>
      </>)
  }
}
