import React from 'react'
import { Link } from 'react-router-dom'
import '../../css/Info.css'
const email='https://mail.google.com/mail/u/0/#inbox'
export default function admin_item(props) {
    const onClick = () =>{      
        props.setFullScreenProps({
            setClickedOnShowQuery:props.setClickedOnShowQuery,
            number:props.number,
            query_id:props.query_id,
            query:props.query,
            date:props.date,
            email:props.email
        })
        props.setClickedOnShowQuery(true)
    }
     const link=props.isLink?<Link onClick={props.setClickedOnShowQuery} to={email}>{props.email}</Link>:props.email
     const query=props.isLink?<button className="btn btn-dark showFullScreenQuery"onClick={onClick}>{props.query}</button>:props.query.substr(0,142)

    return (
    <div className="row my-row justify-content-around">
        <div className="col-2">{props.number}</div>
        <div className="my-col-3 col">{props.query_id}</div>
        <div className="my-col col" styles={{textAlign: 'center'}}>{link}</div>
        <div className="my-col-2 col-2">{query}</div>
        <div className="my-col col"><span>{props.date}</span></div>
    </div>
    )
}
