import React, { useState } from 'react'
import AdminPage from './Admin'
import FullScreen from './items/query_item_fullscreen'
export default function AdminWrapper() {
    const [ClickedOnShowQuery, setClickedOnShowQuery] = useState(false)
    const [FullScreenProps, setFullScreenProps] = useState({})
    return (
        <div>
            {ClickedOnShowQuery ||
             <AdminPage setFullScreenProps={setFullScreenProps} setClickedOnShowQuery={setClickedOnShowQuery} />}
            {ClickedOnShowQuery &&
             <FullScreen setClickedOnShowQuery={setClickedOnShowQuery} allProps={FullScreenProps}/>}
        </div>
    )
}
