import { React, useEffect, useState } from "react";
import { Link } from 'react-router-dom';
export default function Navbar(props) {
  const [CurrentPage, setCurrentPage] = useState("Home")
  const [lastPage, setlastPage] = useState(CurrentPage)
    useEffect(() => {
      document.getElementById(lastPage).classList.remove('active');
      document.getElementById(CurrentPage).classList.add('active');
      setlastPage(CurrentPage);
    }, [CurrentPage,lastPage])
  return (
    <nav className="navbar navbar-expand-lg navbar-dark bg-dark" styles={{marginBottom:'50px'}} >
    <div className="container-fluid">
      <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span className="navbar-toggler-icon"></span>
      </button>
      <div className="collapse navbar-collapse" id="navbarSupportedContent">
        <ul className="navbar-nav me-auto mb-2 mb-lg-0">
          <li className="nav-item">
            <Link className="nav-link active" onClick={()=>{setCurrentPage("Home")}} id="Home" aria-current="page" to="/home">Home</Link>
          </li>
          <li className="nav-item">
            <Link className="nav-link" onClick={()=>{setCurrentPage("About")}} id="About" aria-current="page" to="/about">About</Link>
          </li>
          <li className="nav-item">
            <Link className="nav-link" onClick={()=>{setCurrentPage("Post")}} id="Post" aria-current="page" to="/post">Post</Link>
          </li>
          <li className="nav-item">
            <Link className="nav-link" onClick={()=>{setCurrentPage("Publications")}} id="Publications" aria-current="page" to="/publications">Publications</Link>
          </li>
        </ul>
       <form className="d-flex">
          <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search"/>
          <button className="btn btn-outline-success" type="submit">Search</button>
        </form>
      </div>
    </div>
  </nav>
  );
}
